$taiji-size: 100px;
$animation-time: 3s;

body {
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #e7e7e7; }

.taiji {
  display: inline-flex;
  align-items: center;
  width: $taiji-size;
  height: $taiji-size;
  background: linear-gradient(to bottom, black 50%, white 50%);
  border-radius: 50%;
  animation: taiji-rotate $animation-time linear infinite;

  &::before, &::after {
    content: '';
    width: $taiji-size / 2;
    height: $taiji-size / 2;
    border-radius: inherit; }

  &::before {
    background: radial-gradient(circle, black 14%, white 15%);
    transform-origin: left center;
    animation: taiji-white-scale $animation-time infinite; }

  &::after {
    background: radial-gradient(circle, white 14%, black 15%);
    transform-origin: right center;
    animation: taiji-black-scale $animation-time infinite; } }

@keyframes taiji-rotate {
  to {
    transform: rotate(360deg); } }

@keyframes taiji-white-scale {
  0% {
    transform: scale(.1); }
  50% {
    transform: scale(1.9); }
  100% {
    transform: scale(.1); } }

@keyframes taiji-black-scale {
  0% {
    transform: scale(1.9); }
  50% {
    transform: scale(.1); }
  100% {
    transform: scale(1.9); } }
