body {
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #e7e7e7; }

.taiji {
  display: inline-flex;
  align-items: center;
  width: 100px;
  height: 100px;
  background: linear-gradient(to bottom, black 50%, white 50%);
  border-radius: 50%;
  animation: taiji-rotate 3s linear infinite; }
  .taiji::before, .taiji::after {
    content: '';
    width: 50px;
    height: 50px;
    border-radius: inherit; }
  .taiji::before {
    background: radial-gradient(circle, black 14%, white 15%);
    transform-origin: left center;
    animation: taiji-white-scale 3s infinite; }
  .taiji::after {
    background: radial-gradient(circle, white 14%, black 15%);
    transform-origin: right center;
    animation: taiji-black-scale 3s infinite; }

@keyframes taiji-rotate {
  to {
    transform: rotate(360deg); } }

@keyframes taiji-white-scale {
  0% {
    transform: scale(0.1); }
  50% {
    transform: scale(1.9); }
  100% {
    transform: scale(0.1); } }

@keyframes taiji-black-scale {
  0% {
    transform: scale(1.9); }
  50% {
    transform: scale(0.1); }
  100% {
    transform: scale(1.9); } }

